ding: 19px;\n    width: 94%;\n    left: 38px;\n    pointer-events: none;\n}\n.venue-sarch-cont > div > div {\n    cursor: pointer;\n    margin-bottom: 5px;\n    pointer-events: all;\n}\n\n/* Section-1(search) CSS start */\n.wed-venue-main {\n    display: flex;\n}\n\n.wed-venue-main > div:nth-child(2) {\n    width: 30%;\n}\n\n.wed-venue-search {\n    padding: 20px 0 45px 25px;\n    width: 70%;\n    border-bottom: 1px solid #e1cfd6;\n    transform: rotate(-0.03deg);\n    position: relative;\n}\n\n.wed-venue-search > div:nth-child(1) {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 13.6563px;\n    line-height: 18px;\n    color: #929090;\n}\n\n.wed-venue-search > div:nth-child(2) {\n    font-family: Perpetua;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 25.7763px;\n    line-height: 30px;\n    color: #461952;\n    padding: 10px 0;\n}\n\n.wed-venue-search .wed-ven-main-search > div {\n    margin: 0;\n    width: 100%;\n}\n\n/* Section-1(search) CSS end */\n\n/* Section-2(sidebar) CSS start */\n.wed-venue-content {\n    display: grid;\n    grid-template-columns: minmax(200px, 400px) minmax(70%, auto);\n}\n\n.wed-venue-sidebar {\n    height: 100vh;\n    background-color: white;\n    padding: 23px;\n}\n\n.wed-venue-sidebar-menu > div:nth-child(1) {\n    display: flex;\n    justify-content: space-between;\n    margin-bottom: 1.75em;\n    cursor: pointer;\n}\n\n.wed-venue-sidebar-menu > div:nth-child(1) > span {\n    font-family: Sansation;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 18px;\n    line-height: 20px;\n    color: #313131;\n    padding-left: 10px;\n}\n\n.wed-venue-sidebar-menu > form {\n    padding-left: 20px;\n    display: none;\n    margin-bottom: 1.75em;\n}\n\n.wed-venue-sidebar-menu > form label {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16.4px;\n    line-height: 22px;\n    padding-left: 15px;\n    color: #999999;\n}\n\n.show-drop > form {\n    display: block !important;\n}\n\n/* Section-2(sidebar) CSS end */\n\n/* Section-2(venues) CSS start */\n.wed-venue-items-container > div:nth-child(1) {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16px;\n    line-height: 21px;\n    color: #929090;\n    padding: 20px 20px 0;\n}\n\n#sidebar-open {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16px;\n    line-height: 21px;\n    color: #929090;\n    padding: 20px 15px 0 0;\n    display: none;\n}\n\n.wed-venue-items {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n    grid-template-rows: max-content;\n    grid-column-gap: 10px;\n    grid-row-gap: 20px;\n    padding: 20px;\n}\n\n.wed-venue-items > a {\n    text-decoration: none;\n    color: inherit;\n}\n\n.wed-venue-items > a > div {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n}\n\n.wed-venue-items > a > div > div > div > img {\n    width: 100%;\n    border-radius: 14.6552px;\n    object-fit: cover;\n    height: 328px;\n}\n\n.wed-venue-items-desc {\n    background-color: #ffffff;\n    background: #ffffff;\n    border: 0.146552px solid #000000;\n    border-radius: 14.6552px;\n    padding: 10px;\n    position: relative;\n    bottom: 73px;\n    width: 100%;\n}\n\n.wed-venue-items-desc > div {\n    display: flex;\n    justify-content: space-between;\n    margin: 4px 0;\n}\n\n.wed-venue-items-desc > div > div:nth-child(1) {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 14.6552px;\n    line-height: 19px;\n    color: #000000;\n    align-self: center;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    width: 70%;\n}\n\n.wed-venue-items-desc > div > div:nth-child(2) > div:nth-child(1) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 6.7